import logging from "external:@ruteni/logging/v1";
import { sort } from "external:@ruteni/blaze/v1";
import sanitizer from "external:@ruteni/security/v1";
import { registerElementBundleResolver } from "external:@ruteni/translation/v1";
import QuotquotElement from "external:@ruteni/quotquot/v1:element";
import FluentPlugin from "external:@ruteni/quotquot/v1:plugin.fluent";
import StylePlugin from "external:@ruteni/quotquot/v1:plugin.style";

import html from "./index.html";
import index from "./index.json";

const logger = logging.getLogger("ruteni.app.store.element.page");
// logger.setLevel("WARN");

sanitizer.addCustomElement(index.tagName);
const template = document.createElement("template");
template.innerHTML = sanitizer.sanitize(html);

// TODO: use asset resolution
const styles = index.select["@ruteni/blaze/v1"];
sort(styles);

registerElementBundleResolver(index, import.meta.url);

class UnknownPageHTMLElement extends QuotquotElement {
    constructor() {
        const state = {
            styles: styles.map(name => `/static/@ruteni/blaze/v1/css/${name}.css`)
        };
        super({ template, state, Plugins: [FluentPlugin, StylePlugin] });
    }
}

window.customElements.define(index.tagName, UnknownPageHTMLElement);
